import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import Home from '../views/Home.vue'

const routes: Array<RouteRecordRaw> = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import('../views/Login.vue')
    },
    {
        path: '/form',
        name: 'Form',
        component: () => import('../views/Form.vue')
    },
    {
        path: '/upload_id',
        name: 'File',
        component: () => import('../views/File.vue')
    },
    {
        path: '/upload_certificate',
        name: 'Certificate',
        component: () => import('../views/Certificate.vue')
    }
    
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

router.beforeEach((to, from) => {
    const isLogin = localStorage.nhc_token;
    alert(''+isLogin);
    if(isLogin) {
        const temp = JSON.parse(localStorage.nhc_data);
        const expire = new Date(temp.expired).getTime();
        const current = Date.now();
        if(expire <= current  && to.path != '/login'){
            localStorage.removeItem('nhc_token');
            localStorage.removeItem('nhc_data');
            alert("請重新登入 Please log in again");
            return '/login';
        }
    }else{
        if(to.path != '/login'){
            alert("請先登入 Please log in first");
            return '/login';
        }
    }
    
})

export default router
