
import { defineComponent, ref, watch, computed } from 'vue';
import QRCodeVue3 from "qrcode-vue3";
import langDict from '@/components/language'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { QuickFetch } from '@/components/quick'

export default defineComponent({
    name: 'Home',
    setup() {
        const store = useStore();
        const router = useRouter();
        const qf = new QuickFetch();
        const lang_code = computed(() => store.state.language);
        const lang = ref(langDict(lang_code.value));
        const qrcode_data: any = ref("");
        const qrcode_display: any = ref({type: 'square', color: '#dc2626'});
        //display
        const name: any = ref("");
        const description: any = ref("");
        const signal: any = ref(0);
        const button: any = ref({
            form: 0,
            identity: 0,
            health: 0,
        });
        const qr_color = ['#dc2626','#eab308','#16a34a'];
        watch(lang_code, ()=>{
            lang.value = langDict(lang_code.value);
        });
        const countdown: any = ref("讀取中 Reloading");
        const accept_status = ['v2o', 'v3o', 'pcr', 'rat']

        function codeDisplay(input: any){
            qrcode_data.value = input.code;
            name.value = input.name;
            button.value.form = input.form;
            button.value.identity = input.identity;
            button.value.health = input.health;
            if(input.status=='0000' || input.status=='xxxx' || input.status=='xxxo'){
                description.value = input.status;
                signal.value = 0;
                qrcode_display.value.color = qr_color[signal.value];
            }else{
                signal.value = 0;
                if(input.status.substr(3,1)=='x'){
                    description.value = input.status.substr(3,1);
                    signal.value = 0;
                }else{
                    description.value = input.status.substr(0,3);
                    signal.value = 1;
                    if(accept_status.indexOf(input.status.substr(0,3))>-1){
                        signal.value = 2;
                    }
                }
                qrcode_display.value.color = qr_color[signal.value];
            }
        }

        function getCode(){
            qf.Url('nhc/user').Get().then((res: any)=>{
                codeDisplay(res);
                countdown.value = 30;
            });
        }
        getCode();
        setInterval(()=>{
            getCode();
        },30000);
        setInterval(()=>{
            if(countdown.value>0){
                countdown.value--;
            }else{
                countdown.value = "讀取中 Reloading"
            }
        },1000);
        function logout(){
            localStorage.removeItem('nhc_token');
            localStorage.removeItem('nhc_data');
            router.push('/login');
        }
        return {
            lang_code,
            lang,
            logout,
            qrcode_data,
            qrcode_display,
            countdown,
            name,
            description,
            signal,
            button,
        }
    },
    components: {
        QRCodeVue3
    },
});
